<template>
    <div class="zsMap" ref="zsMap"></div>
</template>

<script>
  // import echarts  from 'echarts';
  import JSON from '../commonJs/guangdongmap.json';

  let mapData = {
    tooltip: {
      trigger: 'item',
      formatter: '{b}<br/>{c} (p / km2)'
    },
    legend: {
      type: "plain",
      show: true,
    },
    visualMap: {
      show: true,
      type:'piecewise',
      padding:[9,9,60,10],
      realtime: false,
      calculable: false,
      textStyle: {
        color: '#ffffff'
      },
      // min: 0,
      // max: 100,
      // text: ['High', 'Low'],
      pieces: [
        // 自定义『分段式视觉映射组件（visualMapPiecewise）』的每一段的范围，以及每一段的文字，以及每一段的特别的样式
        //{ value:0, label: "0", color: "#F1615D" }, // [0]
        { gt: -1, lte: 60, label: "1-60", color: "#F1615D" },
        { gt: 60, lte: 70, label: "61-70", color: "#FFAB00" },
        { gt: 70, lte: 80, label: "71-80", color: "#4389F9" },
        { gt: 80, lte: 90, label: "81-90", color: "#00CCE5" },
        { gt: 90, lte: 100, label: "91-100", color: "#5BC49F" },
      ],
      // inRange: {
      //   color: ['#F1615D', '#FFAB00','#4389F9', '#00CCE5','#5BC49F']
      // }
    },
    series: [{
      type: 'map',
      showLegendSymbol: true,
      map: '广东',       //这里得跟js省份文件名一样得改
      label: {
        show: true,  //显示地图区域名称
        color: "#003987",
        emphasis: {
          show: true
        },
        position: 'top',
        lineHeight: 20,
        // backgroundColor: 'rgba(255,255,255,.9)',
        // borderColor: '#80cffd',
        // borderRadius: 10,
        // borderWidth: '1',
        // padding: [2, 2, 2],
        // color: 'red',
        fontSize: 14,
        fontWeight: 800,
        // fontSize: 14,
        // fontWeight: 'normal',
        formatter: function (params) {
          return params.name + '\n' + params.value;    //地图上展示文字 + 数值
          // return params.name;    //地图上展示文字 + 数值
        },

      },
      itemStyle: {
        normal: {
          // formatter: '{b}111',
          // color: '#ffffff',   //地图小圆点
          color: 'transparent',   //地图小圆点
          opacity: 1,
          borderWidth: 2,
          borderColor: '#cffffb',
        },
        emphasis: {
          areaColor: '#a17504',
        }
      },
      zoom: 1.2,  //地图放大倍数
      data: [
        { name: '板芙镇', value: 73 },
        { name: '大涌镇', value: 85 },
        { name: '东凤镇', value: 67 },
        { name: '东区街道', value: 60 },
        { name: '东升镇', value: 99 },
        { name: '阜沙镇', value: 72 },
        { name: '港口镇', value: 84 },
        { name: '古镇镇', value: 88 },
        { name: '横栏镇', value: 80 },
        { name: '黄圃镇', value: 82 },
        { name: '火炬开发区街道', value: 93 },
        { name: '民众镇', value: 93 },
        { name: '南朗镇', value: 91 },
        { name: '南区街道', value: 90 },
        { name: '南头镇', value: 87 },
        { name: '三角镇', value: 97 },
        { name: '三乡镇', value: 83 },
        { name: '沙溪镇', value: 86 },
        { name: '神湾镇', value: 86 },
        { name: '石岐区街道', value: 0 },
        { name: '坦洲镇', value: 87 },
        { name: '五桂山街道', value: 98 },
        { name: '西区街道', value: 77 },
        { name: '小榄镇', value: 74 },
      ],
      nameMap: {},  //可以格式化data显示
    }]
  }
  export default {
    name: "zs_map",
    data(){
      return {
        mapOption: mapData,//地图数据
      }
    },
    mounted() {
      this.mapFuncton() //初始化地图
    },
    methods:{
      mapFuncton() {
        const echarts2 = require('echarts');
        this.mychart = echarts2.init(this.$refs.zsMap)
        echarts2.registerMap('广东', JSON);
        this.mychart.setOption(this.mapOption);
        window.addEventListener("resize", echarts2.resize);
      },
    }
  }
</script>

<style scoped lang="scss">
    .zsMap{
        height: 100%;
        width: 100%;
    }

</style>